@import "../../assets/styles/variables.scss";
.all-news {
    display: flex;
    flex-direction: column;
    width: 19rem;
    min-height: 100%;
    background: #ffffff;
    border-radius: 0.75rem;
    padding: 1rem;
    @media (max-width: 768px) {
        border-radius: 0;
        padding: 2.5rem 1.5rem;
        margin: 0 -1.5rem;
        width: auto;
        min-height: auto;
    }
    // .all-news__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 2rem;
    }
    // .all-news__title
    &__title {
        font-weight: 600;
        font-size: 1.5rem;
        color: #2d2d2d;
    }
    // .all-news__more
    &__more {
        font-weight: 600;
        font-size: 0.875rem;
        color: $mainDarkColor;
        padding-bottom: 0.125rem;
        border-bottom: 0.0625rem solid currentColor;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
                border-bottom: 0.0625rem solid $accentColor;
            }
        }
    }
    // .all-news__filters
    &__filters {
        margin-bottom: 1rem;
    }
    &__scroll {
        display: flex;
        flex-direction: column;
        min-height: 100%;
        margin-right: -0.5rem;
        margin-left: -1rem;
        @media (max-width: 768px) {
            margin: 0;
        }

        &[data-lastpage="true"] {
            .all-news__content-item:last-child::before {
                display: none;
            }
        }
    }
    // .all-news__content
    &__content {
        flex: 1 1 auto;
        padding: 0 1rem;
        @media (max-width: 768px) {
            padding: 0;
        }
    }
    // .all-news__content-list
    &__content-list {
    }
    // .all-news__content-item
    &__content-item {
        list-style: none;
        &:last-child {
            position: relative;
            &::before {
                content: "";
                position: absolute;
                bottom: 1.5rem;
                left: 0;
                right: 0;
                height: 75px;
                background-image: url(../../assets/images/all-news/shadow.png);
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                pointer-events: none;
            }
        }
    }
    // .all-news__button
    &__button {
        // position: relative;
        // &::before {
        //     content: "";
        //     position: absolute;
        //     bottom: calc(100% + 1rem);
        //     left: -2px;
        //     right: -2px;
        //     height: 75px;
        //     background-image: url(../../assets/images/all-news/shadow.png);
        //     background-size: cover;
        //     background-position: center;
        //     background-repeat: no-repeat;
        //     pointer-events: none;
        // }
    }
}
