@import "../../assets/styles/variables.scss";
.section-articles {
    flex: 1 1 auto;
    // .section-articles__body
    &__body {
    }
    // .section-articles__main-articles
    &__main-articles {
        display: grid;
        gap: 2.5rem 2rem;
        @media (max-width: 992px) {
            display: flex;
            flex-direction: column;
            gap: 2rem;
        }
    }
    // .section-articles__article
    &__article {
        list-style: none;
        &:nth-child(1) {
            grid-column: 1 / 3;
            grid-row: 1 / 2;
        }
        &:nth-child(2) {
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        }
        &:nth-child(3) {
            grid-column: 2 / 3;
            grid-row: 2 / 3;
        }
    }
}
