@import "../../assets/styles/variables.scss";
.item-article {
    padding: 1.5rem 0;
    border-bottom: 0.0625rem solid #ebebeb;
    // .item-article__image
    &__image {
        display: block;
        position: relative;
        max-width: 100%;
        padding-bottom: 56%;
        margin-bottom: 1rem;
        transition: transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
        &:focus-visible {
            outline: 2px solid $accentColor;
            border-radius: 2px;
        }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
    // .item-article__time
    &__time {
        display: inline-block;
        padding-right: 0.25rem;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 140%;
        color: #707070;
    }
    // .item-article__title
    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        font-weight: 600;
        font-size: 1rem;
        line-height: 140%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 768px) {
            -webkit-line-clamp: unset;
        }
    }
}
