@import "../../assets/styles/variables.scss";
.load-more-button {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 0.5rem;
    width: 100%;
    padding: 0.6rem 1.5rem;
    color: $mainDarkColor;
    background-color: transparent;
    border: 0.0625rem solid #979ea9;
    border-radius: 0.25rem;
    margin-top: 1rem;
    cursor: pointer;
    transition: color 0.3s, border 0.3s;
    &.loading {
        color: #979ea9;
        pointer-events: none;
        svg {
            animation: rolling 1s linear infinite;
        }
    }
    svg {
        color: currentColor;
    }
    span {
        display: inline-block;
        height: 1rem;
        font-family: $fontFamily;
        font-weight: 500;
        font-size: 1rem;
        color: inherit;
    }
    @media (any-hover: hover) {
        &:hover {
            color: $accentColor;
            border: 0.0625rem solid $accentColor;
        }
    }
}
@keyframes rolling {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}
