@import "../../assets/styles/variables.scss";
.slide-promo {
    display: flex;
    align-items: flex-start;
    gap: 1rem 2rem;
    @media (max-width: 768px) {
        flex-direction: column;
    }
    // .slide-promo__image
    &__image {
        position: relative;
        flex: 0 0 17rem;
        padding-bottom: 17%;
        transition: transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
        @media (max-width: 768px) {
            width: 100%;
        }
        @media (max-width: 500px) {
            flex: 1 1 auto;
            padding-bottom: 56%;
        }
    }
    // .slide-promo__content
    &__content {
    }
    // .slide-promo__info
    &__info {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 1rem;
        flex-wrap: wrap;
        margin-bottom: 0.625em;
    }
    // .slide-promo__time
    &__time {
        font-weight: 400;
        font-size: 0.75rem;
        line-height: 140%;
        text-transform: uppercase;
        color: $dateTimeColor;
    }
    // .slide-promo__label
    &__label {
        position: relative;
        display: inline-block;
        padding-left: 1.75rem;
        font-size: 0.75rem;
        line-height: 140%;
        color: #979ea9;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 1rem;
            height: 1.25rem;
            background-image: url(../../assets/images/section-four/icons/speaker.svg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }
    }
    // .slide-promo__title
    &__title {
        display: block;
        max-width: 33rem;
        font-weight: 600;
        font-size: 2rem;
        line-height: 130%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 992px) {
            font-size: 1.5rem;
        }
        @media (max-width: 768px) {
            font-size: 1.375rem;
            max-width: unset;
        }
    }
}
