@import "../../assets/styles/variables.scss";
.section-promo {
    padding: 1.5rem 2rem 2rem;
    background-color: #fff;
    border-radius: 0.75rem;
    @media (max-width: 768px) {
        border-radius: 0;
        margin: 0 -1.5rem;
        padding: 1.5rem;
    }
    // .section-promo__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 1.5em;
    }
    // .section-promo__title
    &__title {
    }
    // .section-promo__more
    &__more {
        font-weight: 600;
        font-size: 0.875rem;
        color: $accentColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .section-promo__body
    &__body {
    }
    // .section-promo__promo-slider
    &__promo-slider {
        margin-bottom: 1.5em;
    }
    // .section-promo__promo-thumbs-slider
    &__promo-thumbs-slider {
    }
}
.promo-slider {
    max-width: 100%;
    // .promo-slider__wrapper
    &__wrapper {
    }
    // .promo-slider__slide
    &__slide {
    }
}
.promo-thumbs-slider {
    position: relative;
    max-width: 100%;
    @media (max-width: 768px) {
        margin: 0 -1.5rem;
        max-width: unset;
    }
    // .promo-thumbs-slider__wrapper
    &__wrapper {
    }
    // .promo-thumbs-slider__slide
    &__slide {
    }
    // .promo-thumbs-slider__controls
    &__controls {
        @media (max-width: 768px) {
            display: none;
        }
        .swiper-button-prev,
        .swiper-button-next {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 3rem;
            height: 3rem;
            border-radius: 50%;
            border: 0.0625rem solid transparent;
            background-color: #fff;
            color: #2d2d2d;
            box-shadow: 0px 0.375rem 0.5rem rgba(0, 0, 0, 0.24);
            transition: color 0.3s, border 0.3s, transform 0.3s;
            @media (any-hover: hover) {
                &:hover {
                    color: $accentColor;
                    border: 0.0625rem solid $accentColor;
                }
            }
            &::after {
                display: none;
            }
            svg {
                color: inherit;
            }
        }
        .swiper-button-prev {
            margin-left: 0.5rem;
        }
        .swiper-button-next {
            margin-right: 0.5rem;
        }
    }
    &__control-left,
    &__control-right {
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-50%);
        width: 6.5rem;
        height: calc(100% + 0.125rem);
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
        transition: opacity 0.3s, visibility 0.3s;
        &.swiper-button-disabled {
            opacity: 0;
            visibility: hidden;
            .swiper-button-next,
            .swiper-button-prev {
                transform: scale(0);
            }
        }
    }
    // .promo-thumbs-slider__control-left
    &__control-left {
        left: 0;
        justify-content: flex-start;
        background: linear-gradient(to left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.5) 20%, #ffffff 34.15%);
    }
    // .promo-thumbs-slider__control-right
    &__control-right {
        right: 0;
        justify-content: flex-end;
        background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.5) 20%, #ffffff 34.15%);
    }
}
