@import "../../assets/styles/variables.scss";
.middle-header {
    // .middle-header__desktop
    &__desktop {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2.5rem;
        padding: 0.75rem 0 1rem;
        @media (max-width: 992px) {
            column-gap: 1.25rem;
        }
        @media (max-width: 768px) {
            display: none;
        }
    }
    // .middle-header__menu-list
    &__menu-list {
        display: flex;
        align-items: center;
        column-gap: 2.5rem;
        row-gap: 0.2rem;
        flex-wrap: wrap;
        @media (max-width: 992px) {
            column-gap: 1rem;
        }
    }
    // .middle-header__menu-item
    &__menu-item {
        list-style: none;
    }
    // .middle-header__menu-link
    &__menu-link {
        font-weight: 600;
        font-size: 0.875rem;
        letter-spacing: 0.015em;
        text-transform: uppercase;
        color: $mainDarkColor;
        transition: color 0.3s;
        &--hot {
            display: flex;
            column-gap: 0.5rem;
            align-items: center;
            color: $accentColor;
            svg {
                color: inherit;
                pointer-events: none;
            }
            span {
                color: inherit;
                pointer-events: none;
            }
        }
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
            &--hot:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .middle-header__live
    &__live {
        position: relative;
        display: inline-block;
        padding-left: 1.5rem;
        font-weight: 600;
        font-size: 0.875rem;
        letter-spacing: 0.015em;
        text-transform: uppercase;
        color: $mainDarkColor;
        transition: color 0.3s;
        &::before {
            content: "";
            position: absolute;
            top: 0.0625rem;
            left: 0;
            width: 0.75rem;
            height: 0.75rem;
            background-image: url(../../assets/images/header/icons/live.svg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .middle-header__mobile
    &__mobile {
        margin: 0 -1.5rem;
    }
}
.swiper-menu {
    display: none;
    @media (max-width: 768px) {
        display: block;
        overflow: hidden;
    }
    // .swiper-menu__slide
    &__slide {
        &:first-child {
            padding-left: 1.5rem;
        }
    }
    // .swiper-menu__wrapper
    .swiper-wrapper {
        padding: 0.75rem 0;
    }
}
