@import "../../assets/styles/variables.scss";
// section-two
.section-two {
    padding: 4rem 0 2rem;
    background-color: $backgroundLightColor;
    box-shadow: 0 0 0 100vmax $backgroundLightColor;
    clip-path: inset(0 -100vmax);
    @media (max-width: 768px) {
        padding-top: 0;
    }
    // .section-two__inner
    &__inner {
        display: grid;
        gap: 3.5rem 2rem;
        grid-template-columns: repeat(4, 1fr);
        @media (max-width: 768px) {
            display: flex;
            flex-direction: column;
            row-gap: 2.5rem;
        }
    }
    // .section-two__item
    &__item {
        &:nth-child(1) {
            grid-column: 1 / 2;
            grid-row: 1 / 3;
            @media (max-width: 992px) {
                grid-column: 1 / 2;
                grid-row: 1 / 2;
            }
        }
        &:nth-child(2) {
            grid-column: 2 / 5;
            grid-row: 1 / 2;
        }
        &:nth-child(3) {
            grid-column: 2 / 5;
            grid-row: 2 / 3;
            @media (max-width: 992px) {
                grid-column: 1 / 5;
                grid-row: 2 / 3;
            }
        }
    }
}
