@import "../../assets/styles/variables.scss";
.footer {
    padding: 56px 0;
    background-color: $backgroundLightColor;
    border-top: 0.0625rem solid #dbdbdb;
    border-bottom: 0.0625rem solid #dbdbdb;
    @media (max-width: 768px) {
        padding-top: 24px;
        border-top: none;
    }
    // .footer__inner
    &__inner {
    }
    // .footer__top
    &__top {
    }
    // .footer__middle
    &__middle {
    }
}
.top-footer {
    padding-bottom: 66px;
    @media (max-width: 768px) {
        padding-bottom: 2.5rem;
    }
    // .top-footer__list
    &__list {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(12.5rem, 1fr));
        gap: 1.5rem 1rem;
    }
    // .top-footer__item
    &__item {
        list-style: none;
        display: flex;
        flex-direction: column;
        row-gap: 1.5em;
        align-items: flex-start;
        @media (max-width: 768px) {
            display: block;
            text-align: center;
            margin-top: 2.5rem;
        }
    }
    // .top-footer__link
    &__link {
        font-weight: 600;
        font-size: 1rem;
        color: #2d2d2d;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .top-footer__group
    &__group {
    }
    // .top-footer__menu-list
    &__menu-list {
    }
    // .top-footer__menu-item
    &__menu-item {
        list-style: none;
        margin-bottom: 1.5em;
    }
    // .top-footer__menu-link
    &__menu-link {
        font-weight: 600;
        font-size: 1rem;
        text-transform: uppercase;
        color: #2d2d2d;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
}
.footer-links-group {
    // .footer-links-group__title
    &__title {
        margin-bottom: 0.5em;
        font-weight: 600;
        font-size: 1rem;
        color: #2d2d2d;
    }
    // .footer-links-group__list
    &__list {
    }
    // .footer-links-group__item
    &__item {
        margin-bottom: 0.58em;
        list-style: none;
    }
    // .footer-links-group__link
    &__link {
        font-weight: 400;
        font-size: 0.875rem;
        color: #515151;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
}
.middle-footer {
    // .middle-footer__title
    &__title {
        font-weight: 600;
        font-size: 1.5rem;
        line-height: 140%;
        margin-bottom: 1.33em;
        color: #2d2d2d;
        @media (max-width: 768px) {
            text-align: center;
        }
    }
    // .middle-footer__group
    &__group {
        display: flex;
        align-items: center;
        gap: 1.5em 2em;
        flex-wrap: wrap;
        @media (max-width: 768px) {
            justify-content: center;
        }
    }
    // .middle-footer__list
    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 1.5em 2em;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    // .middle-footer__item
    &__item {
        list-style: none;
    }
    // .middle-footer__link
    &__link {
        position: relative;
        display: inline-block;
        padding-left: 2.43em;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 140%;
        color: #434343;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 1.57em;
            height: 1.57em;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }
        // .middle-footer__link--youtube
        &--youtube::before {
            background-image: url(../../assets/images/footer/icons/yt.svg);
        }
        // .middle-footer__link--telegram
        &--telegram::before {
            background-image: url(../../assets/images/footer/icons/tg.svg);
        }
        // .middle-footer__link--instagram
        &--instagram::before {
            background-image: url(../../assets/images/footer/icons/insta.svg);
        }
        // .middle-footer__link--facebook
        &--facebook::before {
            background-image: url(../../assets/images/footer/icons/fb.svg);
        }
        // .middle-footer__link--tikTok
        &--tikTok::before {
            background-image: url(../../assets/images/footer/icons/tiktok.svg);
        }
        // .middle-footer__link--google-news
        &--google-news::before {
            background-image: url(../../assets/images/footer/icons/gnews.svg);
        }
    }
}
.bottom-footer {
    padding-top: 1.5em;
    padding-bottom: 2em;
    background-color: $backgroundLightColor;
    // .bottom-footer__copyright
    &__copyright {
        font-weight: 400;
        font-size: 0.875rem;
        text-align: center;
        text-transform: uppercase;
        color: #515151;
        margin-bottom: 1.71em;
    }
    // .bottom-footer__list
    &__list {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1.5em 2em;
        flex-wrap: wrap;
    }
    // .bottom-footer__item
    &__item {
        list-style: none;
    }
    // .bottom-footer__link
    &__link {
        font-weight: 400;
        font-size: 0.875rem;
        color: #919191;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
}
