@import "../../assets/styles/variables.scss";
.section-three {
    padding: 3.5rem 0 7rem;
    background-image: url(../../assets/images/section-three/usyk-bg.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #e8e8e8;
    box-shadow: 0 0 0 100vmax #e8e8e8;
    clip-path: inset(0 -100vmax);
    @media (max-width: 992px) {
        background-color: $mainDarkColor;
        background-image: none;
        padding: 3.5rem 0;
    }
    // .section-three__inner
    &__inner {
    }
    // .section-three__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 1.5rem;
    }
    // .section-three__title
    &__title.title {
        color: #fff;
    }
    // .section-three__more
    &__more.more-button {
        color: #fff;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 768px) {
            display: none;
        }
    }
    // .section-three__more-videos
    &__more-videos {
        display: none;
        font-size: 0.875rem;
        color: $accentColor;
        transition: color 0.3s;
        @media (max-width: 768px) {
            display: block;
        }
        @media (any-hover: hover) {
            &:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .section-three__content
    &__content {
    }
    // .section-three__list
    &__list {
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(4, 1fr);
        @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 768px) {
            display: flex;
            flex-direction: column;
            row-gap: 2rem;
            margin-bottom: 1.5rem;
        }
    }
    // .section-three__item
    &__item {
        &:nth-child(1) {
            grid-column: 1 / 3;
            grid-row: 1 / 3;
        }
        &:nth-child(2) {
            grid-column: 3 / 4;
            grid-row: 1 / 2;
            @media (max-width: 992px) {
                grid-column: 1 / 2;
                grid-row: auto;
            }
        }
        &:nth-child(3) {
            grid-column: 4 / 5;
            grid-row: 1 / 2;
            @media (max-width: 992px) {
                grid-column: 2 / 3;
                grid-row: auto;
            }
        }
        &:nth-child(4) {
            grid-column: 3 / 4;
            grid-row: 2 / 3;
            @media (max-width: 992px) {
                grid-column: 1 / 2;
                grid-row: auto;
            }
        }
        &:nth-child(5) {
            grid-column: 4 / 5;
            grid-row: 2 / 3;
            @media (max-width: 992px) {
                grid-column: 2 / 3;
                grid-row: auto;
            }
        }
    }
    // .section-three__button
    &__button {
        color: #fff;
        display: none;
        @media (max-width: 768px) {
            display: flex;
        }
    }
}
