@import "../../assets/styles/variables.scss";
.block-columns {
    background-color: #ffffff;
    border-radius: 0.75rem;
    padding: 1rem 1rem 1.5rem;
    @media (max-width: 1250px) {
        background-color: $backgroundLightColor;
        padding: 0;
    }
    // .block-columns__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 2rem;
    }
    // .block-columns__title
    &__title {
        font-weight: 600;
        font-size: 1.5rem;
        color: #2d2d2d;
    }
    // .block-columns__link
    &__link {
        font-weight: 600;
        font-size: 0.875rem;
        color: $accentColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .block-columns__body
    &__body {
    }
    // .block-columns__list
    &__list {
        @media (max-width: 1250px) {
            display: none;
        }
    }
    // .block-columns__item
    &__item {
        list-style: none;
    }
    // .block-columns__slider
    &__slider {
    }
    // .block-columns__more
    &__more {
        @media (max-width: 1250px) {
            display: none;
        }
    }
}
.block-columns-slider {
    display: none;
    @media (max-width: 1250px) {
        display: block;
        margin: 0 -1.5rem;
    }
    // .block-columns-slider__wrapper
    &__wrapper {
    }
    // .block-columns-slider__slide
    &__slide {
        list-style: none;
    }
}
