.bottom-header {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: flex-end;
    column-gap: 1.25rem;
    background-color: #121b30;
    box-shadow: 0 0 0 100vmax #121b30;
    clip-path: inset(0 -100vmax);
    // .bottom-header__label
    &__label {
        position: relative;
        font-weight: 600;
        font-size: 0.875rem;
        color: #ffffff;
        white-space: nowrap;
        padding: 0.375rem 0;
        &::after {
            @media (max-width: 1340px) {
                content: "";
                position: absolute;
                z-index: 2;
                top: 0;
                left: 100%;
                height: 100%;
                width: 4rem;
                background-image: url(../../assets/images/header/icons/losses-gradient.png);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
        }
    }
}
.losses-slider {
    padding: 0.375rem 0;
    margin-right: -1.5rem;
}
.losses-slide {
    display: flex;
    align-items: flex-end;
    cursor: default;
    // .losses-slide__title
    &__title {
        padding-left: 1rem;
        font-weight: 400;
        font-size: 0.875rem;
        color: #ebebeb;
        white-space: nowrap;
    }
    // .losses-slide__value
    &__value {
        padding-left: 0.25rem;
        font-weight: 400;
        font-size: 0.875rem;
        color: #ff7534;
        white-space: nowrap;
    }
}
