@import "../../assets/styles/variables.scss";
.section-news {
    width: 19rem;
    // .section-news__title
    &__title {
        font-weight: 500;
        font-size: 1rem;
        text-transform: uppercase;
        color: #515151;
        margin-bottom: 2rem;
    }
    // .section-news__body
    &__body {
    }
    // .section-news__list
    &__list {
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
    // .section-news__item
    &__item {
        list-style: none;
        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            border-bottom: 0.0625rem solid $backgroundLightColor;
        }
    }
    // .section-news__more
    &__more {
    }
}
