@import "../../assets/styles/variables.scss";
.section-news-mobile {
    // .section-news-mobile__list
    &__list {
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
    // .section-news-mobile__item
    &__item {
        list-style: none;
        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            border-bottom: 0.0625rem solid $backgroundLightColor;
        }
    }
    // .section-news-mobile__more
    &__more {
    }
}
