@import "../../assets/styles/variables.scss";
.header {
    // .header__top
    &__top {
    }
    // .header__middle
    &__middle {
    }
    // .header__bottom
    &__bottom {
    }
}
.top-header {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 2.5rem;
    min-height: 4rem;
    background-color: #ffffff;
    padding: 1.5625rem 0;
    border-bottom: 0.0625rem solid #e8e8e8;

    @media (max-width: 1100px) {
        column-gap: 1.25rem;
    }
    @media (max-width: 768px) {
        padding: 1rem 0;
    }
    // .top-header__menu-btn
    &__menu-btn {
    }
    // .top-header__navigation
    &__navigation {
        flex: 1 1 auto;
        @media (max-width: 992px) {
            display: none;
        }
    }
    // .top-header__cities-list
    &__cities-list {
        display: flex;
        align-items: center;
        column-gap: 30px;
        row-gap: 15px;
    }
    // .top-header__city-item
    &__city-item {
        list-style: none;
    }
    // .top-header__city-link
    &__city-link {
        font-weight: 600;
        font-size: 0.875rem;
        line-height: 100%;
        text-transform: capitalize;
        text-decoration: none;
        color: #2d2d2d;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .top-header__search
    &__search {
        @media (max-width: 992px) {
            margin-left: auto;
        }
    }
    // .top-header__search-wrapper
    &__search-wrapper {
        @media (max-width: 500px) {
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
        }
    }
    // .top-header__search-form
    &__search-form {
        position: relative;
        width: 17rem;
        height: 2.3rem;
        padding: 0.3rem 1.5rem 0.3rem 2.875rem;
        background-color: #f7f7f7;
        border-radius: 4.25rem;
        &::before {
            position: absolute;
            content: "";
            top: 50%;
            left: 1rem;
            transform: translateY(-50%);
            background-image: url(../../assets/images/header/icons/search-gray.svg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            width: 1.25rem;
            height: 1.25rem;
        }
        &:has(.top-header__search-input:focus-visible) {
            outline: 0.125rem solid $accentColor;
        }
        @media (max-width: 500px) {
            margin-top: 0.25rem;
            width: 100%;
            display: none;
            &.shown {
                display: block;
            }
        }
    }
    // .top-header__search-input
    &__search-input {
        width: 100%;
        height: 100%;
        font-family: TTSevers;
        color: $mainDarkColor;
        background-color: transparent;
        border: none;
        &::placeholder {
            font-family: TTSevers;
            font-weight: 400;
            font-size: 0.875rem;
            line-height: 100%;
            color: #b0b0b0;
        }
        &:focus-visible {
            outline: none;
        }
    }
    // .top-header__search-results
    &__search-results {
        display: none;
    }
    // .top-header__search-btn
    &__search-btn {
        display: none;
        @media (max-width: 500px) {
            display: block;
            border: none;
            background-color: transparent;
            cursor: pointer;
        }
    }
    // .top-header__results-list
    &__results-list {
    }
    // .top-header__result-item
    &__result-item {
    }
    // .top-header__result-link
    &__result-link {
    }
    // .top-header__social-list
    &__social-list {
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 2rem;
        padding-right: 2.375rem;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            height: 1rem;
            width: 0.0625rem;
            background-color: #8a8a8a;
        }
        @media (max-width: 1100px) {
            padding-right: 1.25rem;
        }
        @media (max-width: 992px) {
            display: none;
        }
    }
    // .top-header__social-item
    &__social-item {
        list-style: none;
        max-height: 1.4375rem;
    }
    // .top-header__social-link
    &__social-link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #919191;
        transition: color 0.3s, transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.15);
            }
        }
        svg {
            color: inherit;
        }
        // .top-header__social-link--telegram
        &--telegram {
            @media (any-hover: hover) {
                &:hover {
                    color: #3390ec;
                }
            }
        }
        // .top-header__social-link--facebook
        &--facebook {
            @media (any-hover: hover) {
                &:hover {
                    color: #4267b2;
                }
            }
        }
        // .top-header__social-link--instagram
        &--instagram {
            @media (any-hover: hover) {
                &:hover {
                    color: #c13584;
                }
            }
        }
        // .top-header__social-link--youtube
        &--youtube {
            @media (any-hover: hover) {
                &:hover {
                    color: #fd1d1d;
                }
            }
        }
    }
    // .top-header__mobile-menu
    &__mobile-menu {
    }
    // .top-header__mobile-menu-btn
    &__mobile-menu-btn {
    }
}
.header-menu-btn {
    display: flex;
    align-items: center;
    column-gap: 1rem;
    background-color: #f7f7f7;
    color: #2d2d2d;
    border-radius: 0.25rem;
    padding: 0.5rem 0.75rem;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
    &.opened {
        .header-menu-btn__icon::before {
            transform: rotate(45deg) translateY(0.25rem);
        }
        .header-menu-btn__icon::after {
            transform: rotate(-45deg) translateY(-0.25rem);
            width: 1.125rem;
        }
    }
    @media (any-hover: hover) {
        &:hover {
            color: $accentColor;
        }
    }
    // .header-menu-btn__icon
    &__icon {
        position: relative;
        width: 1.125rem;
        height: 0.5rem;
        pointer-events: none;
        &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 1.125rem;
            height: 0.125rem;
            background-color: currentColor;
            transition: transform 0.3s;
        }
        &::after {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 0.8125rem;
            height: 0.125rem;
            background-color: currentColor;
            transition: transform 0.3s;
        }
    }
    // .header-menu-btn__label
    &__label {
        font-family: TTSevers;
        font-weight: 500;
        font-size: 1rem;
        line-height: 100%;
        transform: translateY(0.125rem);
        pointer-events: none;
    }
}
