@import "../../assets/styles/variables.scss";

.top-header-menu {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    transform: translateX(0);
    width: 100%;
    height: 100vh;
    background-color: $backgroundLightColor;
    overflow-y: auto;
    .top-header__social-list {
        display: flex;
        padding-right: 0;
        justify-content: center;
        &:after {
            display: none;
        }
    }
    @media (min-width: 992px) {
        position: absolute;
        top: 100%;
        width: 20rem;
        max-height: 50vh;
        height: unset;
    }
    &-enter {
        opacity: 0;
        transform: translateX(-150%);
    }
    &-enter-active {
        opacity: 1;
        transform: translateX(0);
        transition: opacity 500ms, transform 500ms;
    }
    &-exit {
        opacity: 1;
        transform: translateX(0);
    }
    &-exit-active {
        opacity: 0;
        transform: translateX(-150%);
        transition: opacity 500ms, transform 500ms;
    }
    // .top-header-menu__container
    &__container {
        display: flex;
        flex-direction: column;
        row-gap: 2rem;
        padding: 1.5rem;
        height: 100%;
        outline: none;
        @media (max-width: 768px) {
            padding: 1rem 1.5rem;
        }
    }
    // .top-header-menu__button
    &__button {
        align-self: flex-start;
        &.header-menu-btn {
            display: none;
            @media (max-width: 992px) {
                display: flex;
            }
        }
    }
}
