@import "../../assets/styles/variables.scss";
.accordion {
    border-top: 1px solid #dedfe0;
    border-bottom: 1px solid #dedfe0;

    &__header {
        min-height: 3.5rem;
        column-gap: 2rem;
    }
    // скидання стилів аккордеону
    &.MuiAccordion-root {
        background-color: $backgroundLightColor;
        &:before {
            display: none;
        }
        &.Mui-expanded {
            margin: 0;
        }
        &.css-1xe9kig-MuiPaper-root-MuiAccordion-root {
            box-shadow: none;
        }
    }
    .MuiAccordionSummary-root {
        min-height: unset;
        padding: 1rem;
        &.Mui-expanded {
        }
    }
    .MuiAccordionSummary-content {
        margin: 0;
        &.Mui-expanded {
        }
    }
    .MuiAccordionDetails-root {
        padding: 0.5rem 2rem 1rem;
    }
}
