@import "../../assets/styles/variables.scss";
.more-button {
    display: flex;
    align-items: center;
    column-gap: 1rem;
    max-width: fit-content;
    color: $mainDarkColor;
    transition: color 0.3s;
    @media (any-hover: hover) {
        &:hover {
            color: $accentColor;
        }
    }
}
