@import "../../assets/styles/variables.scss";
.section-four {
    background-color: #e8e8e8;
    box-shadow: 0 0 0 100vmax #e8e8e8;
    clip-path: inset(0 -100vmax);
    padding: 2rem 0 4rem;
    @media (max-width: 768px) {
        padding-bottom: 1.5rem;
    }
    // .section-four__inner
    &__inner {
        display: flex;
        align-items: flex-start;
        column-gap: 2rem;
        @media (max-width: 1250px) {
            flex-direction: column;
            row-gap: 2rem;
        }
        @media (max-width: 768px) {
            align-items: stretch;
        }
    }
    // .section-four__aside
    &__aside {
        flex: 0 0 19rem;
        @media (max-width: 1250px) {
            flex: 1 1 auto;
            max-width: 100%;
        }
    }
    // .section-four__main
    &__main {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        row-gap: 1.5rem;
        min-width: 0;
        @media (max-width: 1250px) {
            max-width: 100%;
        }
    }
    // .section-four__main-item
    &__main-item {
    }
}
