@import "../../assets/styles/variables.scss";
.main-slider {
    background-color: #e8e8e8;
    box-shadow: 0 0 0 100vmax #e8e8e8;
    clip-path: inset(0 -100vmax);
    overflow: hidden;
    // .main-slider__wrapper
    &__wrapper {
    }
    // .main-slider__slide
    &__slide {
        display: flex;
        align-items: flex-end;
        min-height: 41rem;
        padding: 3.5rem 3.5rem 14.75rem;
        width: 100%;
        height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @media (max-width: 1370px) {
            padding: 3.5rem 1.5rem 14.75rem;
        }
        @media (max-width: 768px) {
            min-height: 30.5rem;
            padding: 1.5rem 1.5rem 8.375rem;
        }
    }
    // .main-slider__slide-text
    &__slide-text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 4;
        overflow: hidden;
        font-weight: 700;
        font-size: 2.5rem;
        line-height: 120%;
        max-width: 47rem;
        color: #ffffff;
        @media (max-width: 768px) {
            font-size: 1.5rem;
        }
    }
    // .main-slider__controls
    &__controls {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0 3.5rem;
        @media (max-width: 1370px) {
            padding: 0 1.5rem;
        }
    }
    // .main-slider__buttons
    &__buttons {
        position: relative;
        display: flex;
        align-items: center;
        column-gap: 1.5rem;
        z-index: 1;
        @media (max-width: 768px) {
            display: none;
        }
    }
    // .main-slider__button
    &__button {
        transition: color 0.3s;
        &.swiper-button-prev,
        &.swiper-button-next {
            color: #ffffff;
        }
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        // .main-slider__button--prev
        &--prev {
            &::after {
                display: none;
            }
        }
        // .main-slider__button--next
        &--next {
            &::after {
                display: none;
            }
        }
        svg {
            color: inherit;
        }
    }
    // .main-slider__pagination
    &__pagination {
        position: static;
        left: unset;
        bottom: unset;
        width: unset;
        min-width: 2.5rem;
        font-weight: 500;
        font-size: 0.875rem;
        color: #fff;
        .swiper-pagination-current {
            opacity: 1;
        }
        .swiper-pagination-total {
            opacity: 0.5;
        }
    }
}
.main-slider-thumbs {
    overflow: hidden;
    padding: 1.5rem 0 3.5rem;
    margin: 0 -5rem;
    @media (max-width: 768px) {
        padding: 1.5rem 0;
    }
    // .main-slider-thumbs__slide
    &__slide {
        display: flex;
        align-items: center;
        column-gap: 1rem;
        width: 19rem;
        min-height: 5rem;
        max-width: 19rem;
        padding: 0.5rem;
        border-radius: 0.75rem;
        background: rgba(255, 255, 255, 0.16);
        cursor: pointer;
        transition: transform 0.3s;
        &.swiper-slide-thumb-active {
            background: rgba(255, 255, 255, 0.32);
            box-shadow: 0 0.375rem 1rem rgba(0, 0, 0, 0.16);
            .main-slider-thumbs__slide-text {
                opacity: 1;
            }
        }
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.05);
            }
        }
        @media (min-width: 1440px) {
            width: unset;
        }
    }
    // .main-slider-thumbs__slide-image
    &__slide-image {
        img {
            width: 4rem;
            height: 4rem;
            object-fit: cover;
            object-position: center;
            border-radius: 0.5rem;
        }
    }
    // .main-slider-thumbs__slide-text
    &__slide-text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        font-weight: 600;
        font-size: 0.875rem;
        line-height: 140%;
        color: #ffffff;
        opacity: 0.7;
    }
}
.main-slider-skeleton {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 41rem;
    @media (max-width: 768px) {
        min-height: 30.5rem;
    }
}
