@import "../../assets/styles/variables.scss";
.block-news {
    padding: 1rem 0;
    &:first-child {
        padding-top: 0;
    }
    &:not(:last-child) {
        border-bottom: 0.0625rem solid #ebebeb;
    }
    // .block-news--bigger-padding
    &--bigger-padding {
        padding: 1.5rem 0;
        &:first-child {
            padding-top: 1.5rem;
        }
    }
    // .block-news--with-photo
    &--with-photo {
        .block-news__label {
            color: #979ea9;
            &::before {
                background-image: url(../../assets/images/all-news/icons/camera.svg);
            }
        }
    }
    // .block-news--hot
    &--hot {
        .block-news__label {
            color: $accentColor;
            &::before {
                background-image: url(../../assets/images/all-news/icons/fire.svg);
            }
        }
        .block-news__link {
            font-weight: 600;
        }
    }
    // .block-news--video
    &--video {
        .block-news__label {
            color: #979ea9;
            &::before {
                background-image: url(../../assets/images/all-news/icons/video.svg);
            }
        }
    }
    // .block-news--exclusive
    &--exclusive {
        .block-news__label {
            color: $accentColor;
            &::before {
                width: 1.125rem;
                background-image: url(../../assets/images/all-news/icons/exclusive.svg);
            }
        }
        .block-news__link {
            font-weight: 600;
        }
    }
    // .block-news--online
    &--online {
        .block-news__label {
            color: $accentColor;
            &::before {
                background-image: url(../../assets/images/all-news/icons/online.svg);
            }
        }
        .block-news__link {
            font-weight: 600;
        }
    }
    // .block-news__link
    &__link {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 4;
        overflow: hidden;
        font-weight: 500;
        font-size: 1rem;
        line-height: 140%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        time {
            display: inline-block;
            padding-right: 0.25rem;
            font-weight: 400;
            font-size: 0.875rem;
            line-height: 140%;
            color: $dateTimeColor;
        }
    }
    // .block-news__label
    &__label {
        position: relative;
        font-weight: 600;
        font-size: 0.875rem;
        padding-left: 1.75rem;
        margin-bottom: 1rem;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 1rem;
            height: 1.25rem;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }
    }
}
