@import "../../assets/styles/variables.scss";
.block-article {
    // block-article--with-label
    &--with-label {
        .block-article__info {
            display: flex;
            align-items: center;
            column-gap: 1rem;
            margin-bottom: 0.75rem;
        }
        .block-article__time {
            margin-bottom: 0;
        }
        .block-article__label {
            position: relative;
            padding-left: 1.75rem;
            font-weight: 600;
            font-size: 0.875rem;
            color: #979ea9;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 1rem;
                height: 1.25rem;
                background-image: url(../../assets/images/section-four/icons/microphone.svg);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
        }
    }
    // .block-article__image
    &__image {
        display: block;
        position: relative;
        max-width: 100%;
        padding-bottom: 56%;
        margin-bottom: 1rem;
        transition: transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
    // .block-article__time
    &__time {
        display: block;
        color: #707070;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 0.75rem;
        margin-bottom: 0.65rem;
    }
    // .block-article__title
    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        font-weight: 600;
        font-size: 1rem;
        line-height: 140%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 768px) {
            -webkit-line-clamp: unset;
            font-size: 1.375em;
        }
        &--large {
            font-size: 1.5rem;
            @media (max-width: 992px) {
                font-size: 1rem;
            }
            @media (max-width: 768px) {
                font-size: 1.375rem;
            }
        }
    }
}
