@import "assets/styles/fonts.scss";
@import "assets/styles/variables.scss";

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: $fontFamily;
    font-weight: 500;
    font-size: 1rem;
    color: $mainDarkColor;
    &.locked {
        overflow: hidden;
    }
}
a {
    font-family: inherit;
    font-size: inherit;
    text-decoration: none;
}
.wrapper {
    max-width: 1440px;
    margin: 0 auto;
}
.container {
    max-width: 1363px;
    padding: 0 1.5rem;
    margin: 0 auto;
}
.title {
    font-weight: 700;
    font-size: 2rem;
    color: #2d2d2d;
    @media (max-width: 768px) {
        font-size: 1.875rem;
    }
}
.swiper-slide {
    width: unset;
}
.swiper-button-prev,
.swiper-button-next {
    position: static;
    top: unset;
    width: unset;
    height: unset;
    margin-top: unset;
    z-index: unset;
    cursor: pointer;
    display: block;
    align-items: center;
    justify-content: center;
    color: unset;
}
// first screen
.first-screen {
    position: relative;
    margin-top: -0.0625rem;
    // .first-screen__slider
    &__slider {
    }
    // .first-screen__thumbs
    &__thumbs {
    }
}
