@import "../../assets/styles/variables.scss";
.tablist {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    flex-wrap: wrap;
}
.tab {
    list-style: none;
    padding: 0.25rem 0.5rem;
    font-weight: 500;
    line-height: 140%;
    color: #919191;
    border-radius: 0.25rem;
    border: none;
    cursor: pointer;
    transition: color 0.3s, background-color 0.3s;
    &.selected {
        color: $accentColor;
        background-color: #ffeae0;
    }
    @media (any-hover: hover) {
        &:hover {
            color: $accentColor;
            background-color: #ffeae0;
        }
    }
}
