@import "../../assets/styles/variables.scss";
.item-video {
    list-style: none;
    // .item-video--large
    &--large {
        .item-video__link {
            max-width: 40rem;
            margin-bottom: 2rem;
            &::before {
                width: 2.5rem;
                height: 2.5rem;
            }
            @media (any-hover: hover) {
                &:hover {
                    .item-video__preview {
                        transform: scale(1.025);
                    }
                }
            }
            @media (max-width: 992px) {
                max-width: unset;
            }
        }
        .item-video__preview {
        }
        .item-video__time {
            margin-bottom: 1.5rem;
            @media (max-width: 768px) {
                margin-bottom: 0.5rem;
            }
        }
        .item-video__title {
            font-size: 2rem;
            @media (max-width: 768px) {
                font-size: 1.375rem;
            }
        }
    }
    // .item-video__link
    &__link {
        position: relative;
        display: block;
        max-width: 19rem;
        padding-bottom: 56%;
        margin-bottom: 1rem;
        &::before {
            content: "";
            position: absolute;
            z-index: 1;
            left: 1rem;
            bottom: 1rem;
            width: 1.25rem;
            height: 1.25rem;
            background-image: url(../../assets/images/section-three/icons/play.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
        @media (any-hover: hover) {
            &:hover {
                .item-video__preview {
                    transform: scale(1.05);
                }
            }
        }
        @media (max-width: 992px) {
            max-width: unset;
        }
    }
    // .item-video__preview
    &__preview {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        transition: transform 0.3s;
    }
    // .item-video__time
    &__time {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: 400;
        font-size: 0.75rem;
        text-transform: uppercase;
        color: #fff;
    }
    // .item-video__title
    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-weight: 600;
        font-size: 1rem;
        line-height: 130%;
        color: #fff;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 768px) {
            font-size: 1.375rem;
            -webkit-line-clamp: unset;
        }
    }
}
