// .block-article-skeleton
.block-article-skeleton {
    list-style: none;
    &:nth-child(1) {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
        .block-article-skeleton__image.MuiSkeleton-root {
            width: 100%;
            height: 360px;
            @media (max-width: 992px) {
                height: 150px;
            }
        }
    }
    &:nth-child(2) {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        .block-article-skeleton__image.MuiSkeleton-root {
            width: 100%;
            height: 170px;
            @media (max-width: 992px) {
                height: 150px;
            }
        }
    }
    &:nth-child(3) {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
        .block-article-skeleton__image.MuiSkeleton-root {
            width: 100%;
            height: 170px;
            @media (max-width: 992px) {
                height: 150px;
            }
        }
    }
}
