.news-articles-mobile {
    // .news-articles-mobile__controls
    &__controls {
        margin-bottom: 2rem;
    }
    // .news-articles-mobile__tab
    &__tab {
    }
    // .news-articles-mobile__body
    &__body {
    }
}
.news-articles-mobile-animation {
    &-enter {
        opacity: 0;
        visibility: hidden;
        transform: translateX(-50%);
    }
    &-enter-active {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        transition: all 0.5s;
    }
    &-exit {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }
    &-exit-active {
        opacity: 0;
        visibility: hidden;
        transform: translateX(50%);
        transition: all 0.5s;
    }
}
