@import "../../assets/styles/variables.scss";
.card-region {
    display: flex;
    flex-direction: column;
    row-gap: 1.5rem;
    max-width: 19.687rem;
    background: #ffffff;
    border-radius: 0.75rem;
    padding: 1rem 1rem 2rem;
    @media (max-width: 375px) {
        max-width: calc(100vw - 3rem);
    }
    // .card-region__title
    &__title {
        font-size: 2rem;
        color: #2d2d2d;
    }
    // .card-region__content
    &__content {
        flex: 1 1 auto;
    }
    // .card-region__news
    &__news {
    }
    // .card-region__day
    &__day {
        &:not(:last-child) {
            margin-bottom: 1.5rem;
        }
    }
    // .card-region__news-date
    &__news-date {
        display: block;
        font-weight: 400;
        font-size: 0.75rem;
        text-transform: uppercase;
        color: $accentColor;
        margin-bottom: 0.75rem;
    }
    // .card-region__list
    &__list {
    }
    // .card-region__item
    &__item {
        list-style: none;
    }
    // .card-region__more
    &__more {
    }
}
