@import "../../assets/styles/variables.scss";
.regions {
    // .regions__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 2rem;
    }
    // .regions__title
    &__title {
    }
    // .regions__tablist
    &__tablist {
        margin-bottom: 1.5rem;
        // тимчасова альтернатива слайдеру
        display: none;
        @media (max-width: 1400px) {
            display: flex;
        }
    }
    // .regions__tab
    &__tab {
    }

    // .regions__link
    &__link {
        font-weight: 600;
        font-size: 0.875rem;
        color: $accentColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .regions__body
    &__body {
    }
    // .regions__list
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 1rem;

        // тимчасова альтернатива слайдеру
        overflow-x: auto;
    }
    // .regions__item
    &__item {
        list-style: none;
        min-width: 19rem;
    }
    // .regions__tablist
    &__tablist {
        @media (max-width: 992px) {
            padding: 0 1.5rem;
        }
    }
}
.regions-slider {
    @media (max-width: 992px) {
        margin: 0 -1.5rem;
    }
    &__slide {
        list-style: none;
        height: unset;
        min-height: 100%;
    }
}
