.editors-choice-skeleton {
    &__list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(14rem, 1fr));
        gap: 2.5rem 2rem;
        @media (max-width: 450px) {
            grid-template-columns: auto;
        }
    }
    &__item {
        list-style: none;
        span {
            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
        }
    }
}
