@import "../../assets/styles/variables.scss";
.menu {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    background-color: $backgroundLightColor;
    // .menu__icon
    &__icon {
    }
    // .menu__title
    &__title {
        margin-bottom: 0.5em;
        font-weight: 600;
        font-size: 1rem;
        color: #2d2d2d;
    }
    // .menu__list
    &__list {
    }
    // .menu__item
    &__item {
        margin-bottom: 0.58em;
        list-style: none;
    }
    // .menu__link
    &__link {
        font-weight: 400;
        font-size: 0.875rem;
        color: #515151;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
}
.top-menu {
    &__list {
    }
    &__item {
        list-style: none;
        border-top: 1px solid #dedfe0;
        border-bottom: 1px solid #dedfe0;
        &:first-child {
            border-top: none;
        }
        &:last-child {
            border-bottom: 1px solid transparent;
        }
    }
    &__link {
        display: inline-block;
        padding: 1rem;
        font-weight: 600;
        font-size: 1rem;
        color: #2d2d2d;
        outline-offset: -1px;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
}
