@import "../../assets/styles/variables.scss";
.top-header {
    // .top-header__languages-list
    &__languages-list {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 1rem;
        @media (max-width: 992px) {
            z-index: 1;
        }
        @media (max-width: 768px) {
            display: none;
        }
    }
    // .top-header__language-item
    &__language-item {
        list-style: none;
        max-height: 1rem;
        &.selected {
            .top-header__language-link {
                color: #b0b0b0;
                cursor: default;
                pointer-events: none;
            }
        }
    }
    // .top-header__language-link
    &__language-link {
        font-weight: 500;
        font-size: 1rem;
        line-height: 100%;
        text-transform: uppercase;
        color: #2d2d2d;
        text-decoration: none;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .top-header__select-language
    &__select-language {
        @media (min-width: 768px) {
            display: none;
        }
    }
}
.select-language {
    position: relative;

    // .select-language__button
    &__button {
        display: flex;
        align-items: center;
        column-gap: 0.2rem;
        background-color: transparent;
        border: none;
        font-family: TTSevers;
        color: #2d2d2d;
        font-weight: 500;
        font-size: 0.875rem;
        line-height: 100%;
        cursor: pointer;
        transition: color 0.3s;
        &.opened {
            .select-language__icon {
                transform: rotate(180deg) translateY(0.1875rem);
            }
        }
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .select-language__label
    &__label {
    }
    // .select-language__icon
    &__icon {
        transform: translateY(-0.0625rem);
        color: inherit;
        transition: transform 0.3s;
    }
    // .select-language__body
    &__body {
        position: absolute;
        top: 150%;
        right: 0;
        background-color: #ffeae0;
        border-radius: 0.5rem;
        z-index: 2;
    }
    // .select-language__list
    &__list {
    }
    // .select-language__item
    &__item {
        list-style: none;
        button {
            padding: 0.5rem 0.3rem;
            min-width: 5rem;
            border: none;
            background-color: transparent;
            font-family: TTSevers;
            color: #2d2d2d;
            font-weight: 500;
            font-size: 0.875rem;
            line-height: 100%;
            cursor: pointer;
            transition: color 0.3s;
            @media (any-hover: hover) {
                &:hover {
                    color: $accentColor;
                }
            }
        }
        &.selected {
            button {
                color: $accentColor;
            }
        }
    }
}
.language-animation {
    &-enter {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transform: translateY(50%);
    }
    &-enter-active {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        pointer-events: all;
        transition: opacity 0.5s, transform 0.5s, visibility 0.5s;
    }
    &-exit {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        pointer-events: all;
    }
    &-exit-active {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transform: translateY(50%);
        transition: opacity 0.5s, transform 0.5s, visibility 0.5s;
    }
}
