@import "../../assets/styles/variables.scss";
.main-section {
    padding: 1.5rem 2rem 2rem;
    background-color: #fff;
    border-radius: 0.75rem;
    @media (max-width: 768px) {
        border-radius: 0;
        margin: 0 -1.5rem;
        padding: 1.5rem;
        overflow-x: hidden;
    }
    // .main-section__header
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 2rem;
        margin-bottom: 2rem;
    }
    // .main-section__title
    &__title {
    }
    // .main-section__more
    &__more {
        font-weight: 600;
        font-size: 0.875rem;
        color: $accentColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentHoverColor;
            }
        }
    }
    // .main-section__content
    &__content {
        display: flex;
        align-items: flex-start;
        column-gap: 2rem;
    }
    // .main-section__item
    &__item {
        &:first-child {
            flex: 1 1 auto;
        }
        @media (max-width: 768px) {
            display: none;
        }
    }
    // .main-section__mobile
    &__mobile {
        display: none;
        @media (max-width: 768px) {
            display: block;
            flex: 1 1 auto;
        }
    }
}
