@import "../../assets/styles/variables.scss";
.editors-choice {
    // .editors-choice__header
    &__header {
        margin-bottom: 2rem;
    }
    // .editors-choice__title
    &__title {
    }
    // .editors-choice__list
    &__list {
        list-style: none;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(14rem, 1fr));
        gap: 2.5rem 2rem;
        @media (max-width: 450px) {
            grid-template-columns: auto;
        }
    }
    // .editors-choice__item
    &__item {
    }
    // .editors-choice__link
    &__link {
        position: relative;
        max-width: 100%;
        padding-bottom: 56%;
        display: block;
        margin-bottom: 1rem;
        transition: transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
    }
    // .editors-choice__image
    &__image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
    // .editors-choice__time
    &__time {
        display: block;
        margin-bottom: 0.65rem;
        font-weight: 400;
        font-size: 0.75rem;
        text-transform: uppercase;
        color: $dateTimeColor;
    }
    // .editors-choice__item-title
    &__item-title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        font-weight: 600;
        font-size: 1rem;
        line-height: 130%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 768px) {
            font-size: 1.375rem;
        }
    }
    // .editors-choice__button
    &__button {
        display: none;
        @media (max-width: 768px) {
            display: flex;
        }
    }
}
