@import "../../assets/styles/variables.scss";
.promo-thumb-slide {
    background: #f7f7f7;
    border-radius: 0.5rem;
    padding: 1rem;
    max-width: 19rem;
    cursor: pointer;
    // .promo-thumb-slide__time
    &__time {
        display: block;
        font-weight: 400;
        font-size: 0.75rem;
        text-transform: uppercase;
        color: $dateTimeColor;
        margin-bottom: 0.715em;
    }
    // .promo-thumb-slide__title
    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }
}
