@import "../../assets/styles/variables.scss";
.item-column {
    padding: 1.5rem 0;
    border-top: 0.0625rem solid $backgroundLightColor;
    @media (max-width: 1250px) {
        display: flex;
        flex-direction: column;
        padding: 1rem;
        background-color: #ffffff;
        border-radius: 0.75rem;
        max-width: 19rem;
        min-height: 14rem;
        border-top: none;
    }
    &:first-child {
        padding-top: 0;
        border-top: none;
        @media (max-width: 1250px) {
            padding-top: 1rem;
        }
    }
    // .item-column__top
    &__top {
        display: flex;
        align-items: center;
        column-gap: 1rem;
        margin-bottom: 1rem;
    }
    // .item-column__photo
    &__photo {
        width: 4rem;
        height: 4.75rem;
        object-fit: cover;
        object-position: center;
        transition: transform 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
    }
    // .item-column__author
    &__author {
    }
    // .item-column__name
    &__name {
        display: inline-block;
        font-weight: 500;
        font-size: 1rem;
        color: #2d2d2d;
        margin-bottom: 0.25rem;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
    }
    // .item-column__label
    &__label {
        font-weight: 400;
        font-size: 0.75rem;
        text-transform: uppercase;
        color: $dateTimeColor;
    }
    // .item-column__article
    &__article {
        display: inline-block;
        margin-bottom: 0.5rem;
        font-weight: 600;
        font-size: 1rem;
        line-height: 130%;
        color: $mainDarkColor;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: $accentColor;
            }
        }
        @media (max-width: 1250px) {
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
            margin-bottom: auto;
        }
    }
    // .item-column__time
    &__time {
        display: block;
        color: $dateTimeColor;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 0.75rem;
    }
}
